<template>
    <div class="ui-inbox-box">
        <grid :columns="columns" :data="data"></grid>
    </div>
</template>

<script>
import {description, stateName, stateAssigneeName, stateEnteredAt, viewButtons} from './columns';
import Grid from '../../common/grid';

export default {
    props: {
        data: Array
    },
    components: {
        Grid
    },
    data() {
        return {
            columns: [
                description(),
                stateName(),
                stateAssigneeName(),
                stateEnteredAt(),
                viewButtons()
            ]
        }
    }
}
</script>

