<template>
    <div id="app">
        <dashboard config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout"
                   @ready="onDashboardReady">
            <template v-if="ready">
                <router-view></router-view>
            </template>
        </dashboard>
    </div>
</template>

<script>
export default {
    data() {
        return {
            ready: false
        }
    },
    methods: {
        onDashboardReady(config) {
            this.ready = true;
            // this.$widgets = [];
            // this.$apps = {};
            config.Apps.forEach((app) => {
                if (app.Widgets) {
                    app.Widgets.forEach((w) => {
                        if (w.Enabled) {
                            if (Affinity.user.EmployeeNo >= 5000000 && w.Name != 'Inbox') {
                                return;
                            }
                            this.$widgets.push(w.Name);
                        }
                    });
                }
                this.$apps[app.Name] = app;
            });
        }
    }
}
</script>

<style>
#app {
    height: 100%;
}
</style>