<template>
    <tile
        v-if="showWidget"
        icon="icon-plane"
        title="My Leave"
        class="my-leave-widget"
        :loading="loading">
        <div v-if="error">{{ error }}</div>
        <div v-else>
            <div v-if="annual.show" class="detailRow green">
                <div class="numTile">
                    <div class="style"></div>
                    <div class="value">{{ floor(annual.days) }}</div>
                </div>
                <div class="numValue">
                    <div class="title">{{ annual.title }}</div>
                    <div class="hours">{{ floor(annual.hoursDisplay) }} hours as at {{ annual.date }} ({{
                            annual.subText
                        }})
                    </div>
                </div>
            </div>
            <div v-if="longservice.show" class="detailRow blue">
                <div class="numTile">
                    <div class="style"></div>
                    <div class="value">{{ floor(longservice.days) }}</div>
                </div>
                <div class="numValue">
                    <div class="title">{{ longservice.title }}</div>
                    <div class="hours">{{ floor(longservice.hoursDisplay) }} hours as at {{ longservice.date }}
                        ({{ longservice.subText }})
                    </div>
                </div>
            </div>
            <div v-if="sick.show" class="detailRow orange">
                <div class="numTile">
                    <div class="style"></div>
                    <div class="value">{{ floor(sick.days) }}</div>
                </div>
                <div class="numValue">
                    <div class="title">{{ sick.title }}</div>
                    <div class="hours">{{ floor(sick.hoursDisplay) }} hours as at {{ sick.date }} ({{
                            sick.subText
                        }})
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>

<script>
import axios from "axios";
import Tile from "../common/tile"
import moment from "moment";

class Leave {
    constructor(title, date) {
        this.title = title;
        this.date = date;
        this.days = 0;
        this.hours = 0;
        this.entitled = 0;
        this.show = false;
        this.hoursDisplay = 0;
        this.stdHoursPerDay = 0;
    }
}

export default {
    components: {
        Tile
    },
    data() {
        return {
            loading: true,
            annual: null,
            longservice: null,
            sick: null,
            error: null
        };
    },
    methods: {
        reset() {
            this.annual = new Leave("Annual Leave");
            this.longservice = new Leave("Long Service Leave");
            this.sick = new Leave("Personal Leave");
        },
        add(leave, data) {
            leave.title = data.CodeDescription;
            leave.date = moment(data.PeriodEndDate).format('ddd DD MMM YYYY');

            leave.hours += data.TotalHours;
            leave.entitled += data.Entitlement;
            leave.stdHoursPerDay += data.StdHoursPerDay;
            leave.show = true;
            if (data.IncludeFutureLeave) {
                leave.subText = "inc. future leave";
                leave.hoursDisplay = leave.hours;
                leave.days += data.TotalDays;
            } else {
                leave.subText = "exc. future leave";
                leave.hoursDisplay = leave.entitled;
                leave.days += (leave.entitled / leave.stdHoursPerDay)
            }
        },
        floor(value) {
            return Math.floor(value);
        }
    },
    computed: {
        showWidget() {
            return (
                this.loading ||
                this.annual.show ||
                this.sick.show ||
                this.longservice.show
            );
        }
    },
    created() {
        this.reset();

        axios
            .post("/Leave/GetBalances")
            .then(resp => {
                if (resp.data.error) {
                    return console.log(data.error);
                }

                let data = resp.data.Data;

                this.reset();

                if (data && data.ComponentBalances) {
                    data.ComponentBalances.forEach(compBal => {
                        compBal.CodeBalances.forEach(codeBal => {
                            switch (codeBal.LeaveCode) {
                                case "07":
                                    this.add(this.sick, codeBal);
                                    break;
                                case "09":
                                    this.add(this.annual, codeBal);
                                    break;
                                case "10":
                                    this.add(this.longservice, codeBal);
                                    break;
                            }
                        });
                    });
                }
            })
            .catch(err => {
                this.error = "Could not load leave balances.";
            })
            .then(() => {
                this.loading = false;
            });
    }
};
</script>


<style lang="scss">
$leave-tile-width: 70px;
$button-blue: #44b5ec;
$button-green: #7abd2d;
$button-yellow: #ffbd00;
$button-orange: #ff6600;

.my-leave-widget {
    .detailRow {
        clear: both;
        float: none;
        height: 60px;
        margin: 0 0 5px 0;
        color: #666;
    }

    .detailRow .numTile {
        position: relative;
        float: left;
        width: $leave-tile-width;
        height: 60px;
        background-color: #fff;
        border-radius: 5px;
    }

    .detailRow .numTile .style {
        position: absolute;
        top: 29px;
        width: $leave-tile-width;
        height: 28px;
        border-top: 2px solid #f3f3f3;
    }

    .detailRow .numTile .value {
        position: absolute;
        top: 0;
        width: $leave-tile-width;
        height: 60px;
        line-height: 60px;
        font-size: 30px;
        text-align: center;
        letter-spacing: -3px;
    }

    .detailRow .numValue {
        margin-left: $leave-tile-width + 10px;
        margin-right: 30%;
    }

    .detailRow .numValue .title {
        /*height: 30px;
                        line-height: 40px;*/
        margin: 0;
        font-size: 16px;
        font-weight: normal;
    }

    .detailRow .numValue .hours {
        font-size: 12px;
        margin-top: 5px;
    }

    .detailRow .entitlement {
        float: right;
        width: 30%;
        /*margin: 15px 0 0 0;*/
        font-size: 12px;
        text-align: right;
        /*line-height: 20px;*/
    }

    .detailRow.blue .numTile .value {
        color: lighten($button-blue, 10%);
    }

    .detailRow.green .numTile .value {
        color: lighten($button-green, 10%);
    }

    .detailRow.orange .numTile .value {
        color: lighten($button-orange, 10%);
    }
}
</style>
