<template>
    <tile title="My Forms" icon="icon-page" :nopadding="!loading" class="my-forms-widget" :loading="loading">
        <div v-if="error" class="error-text">{{ error }}</div>
        <div v-else class="section">
            <div class="section-body nopadding">
                <div class="ui-inbox">
                    <div class="ui-inbox-labels">
                        <div class="button blue startnew" @click="openNewFormModal">Start New Form</div>
                        <span class="ui-inbox-loader" v-if="refreshing"></span>
                        <br/><br/>
                        <ul>
                            <li v-for="tab of tabs" @click="onTabClick(tab)"
                                :class="{ selected: selectedTab == tab.name }">
                                <span class="indicator">{{ tab.count > 99 ? '99+' : tab.count }}</span>
                                <span class="boxicon fonticon" :class="'icon-' + tab.icon"></span>
                                <span class="label">{{ tab.label }}</span>
                            </li>
                        </ul>
                    </div>
                    <div class="ui-inbox-boxes">
                        <component v-on:refreshgrid="refreshGrid" :is="selectedTab" :data="currentPageData"></component>
                        <pager v-if="currentData.length > 6" :row-count="currentData.length" :page-size="pageSize"
                               :page="page" item-label="form(s)" @goto="goToPage"></pager>
                    </div>
                </div>
            </div>
        </div>
        <modal :visible="newFormModalVisible" @close="onNewFormModalClose">
            <div class="spinnerbox large" v-if="formsLoading">
                <img :src="formsLoadImg"/>
            </div>
            <div class="section" v-else>
                <form action="/CleverForms/StartNewForm">
                    <div class="default-form">
                        <div class="form-row">
                            <strong>Select a form to start</strong>
                        </div>
                        <div class="form-row">
                            <select ref="formSelector" class="width-full" name="templateAndWorkflowIds">
                                <option v-for="f of forms"
                                        :value="f ? f.TemplateId + ';' + f.WorkflowDefinitionId : ''">
                                    {{ f ? f.TemplateDescription + ' - ' + f.WorkflowName : '' }}
                                </option>
                            </select>
                        </div>
                        <div class="form-row">
                            <button class="green" type="submit" @click="onFormStart($event)">
                                <span class="icon-tick"></span>
                                Start
                            </button>
                            <div class="button grey" @click="onNewFormModalClose">
                                <span class="icon-cancel"></span>
                                Cancel
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </modal>
    </tile>
</template>

<script>
import Vue from 'vue';
import ToAction from './to-action-box.vue';
import InProgress from './in-progress-box.vue';
import Completed from './completed-box.vue';
import Tile from '../../common/tile';
import Modal from '../../common/modal';
import axios from "axios";

export default {
    components: {
        ToAction,
        InProgress,
        Completed,
        Tile,
        Modal
    },
    data() {
        return {
            tabs: [{
                name: 'ToAction',
                icon: 'download',
                label: 'To Action',
                count: '0'
            }, {
                name: 'InProgress',
                icon: 'clock',
                label: 'In Progress',
                count: '0'
            }, {
                name: 'Completed',
                icon: 'box',
                label: 'Completed',
                count: '0'
            }],
            data: {
                ToAction: [],
                InProgress: [],
                Completed: []
            },
            forms: [],
            formsLoading: false,
            formsLoadImg: document.config.loaders.large.light,
            newFormModalVisible: false,
            selectedTab: 'ToAction',
            loading: true,
            refreshing: false,
            user: Affinity.user,
            page: 0,
            pageSize: 6,
            error: null
        }
    },
    computed: {
        currentData() {
            return this.data[this.selectedTab] || [];
        },
        currentPageData() {
            let data = [];
            let offset = this.page * this.pageSize;
            let threshold = Math.min(offset + this.pageSize, this.currentData.length);

            for (let i = offset; i < threshold; i++) {
                data.push(this.currentData[i]);
            }

            return data;
        }
    },
    methods: {
        openNewFormModal() {
            this.formsLoading = true;
            this.newFormModalVisible = true;

            this.getAvailableForms(() => {
                new UIAutoComplete();
                new UIAutoCompleteWidget({
                    selectElement: this.$refs.formSelector
                });
            });
        },
        onTabChanged(tab) {
            this.selectedTab = tab;
            this.getData();
        },
        onNewFormModalClose() {
            this.newFormModalVisible = false;
        },
        onFormStart(evt) {
            if (!this.$refs.formSelector.value) {
                evt.preventDefault();
                return false;
            }
        },
        getAvailableForms(callback) {
            axios.post('/CleverForms/GetAvailableForms')
                .then((response) => {
                    this.forms = response.data;
                    this.forms.unshift(null);
                    this.formsLoading = false;
                    this.newFormModalVisible = false; //Forcing iOS to redraw

                    Vue.nextTick(() => {
                        this.newFormModalVisible = true; //Forcing iOS to redraw
                        if (callback) {
                            Vue.nextTick(() => {
                                callback();
                            });
                        }
                    });
                }, () => {
                    console.log('request failed');
                }).then(() => {
                this.loading = false;
            })
        },
        refreshGrid() {
            this.getData();
        },
        getData() {
            this.refreshing = true;
            axios.post('/CleverForms/GetInboxData')
                .then(response => {
                    console.log('inbox data', response.data.data);
                    for (let name in this.tabMap) {
                        this.tabMap[name].count = response.data.data[name + 'Count'] || '0';
                        this.data[name] = response.data.data[name];
                    }
                    console.log('data', this.data);
                    this.refreshing = false;
                }, () => {
                    this.error = 'Could not load forms.'
                }).then(() => {
                this.loading = false;
            })
        },
        goToPage(num) {
            this.page = num;
        },
        onTabClick(tab) {
            this.selectedTab = tab.name;
            this.page = 0;
        }
    },
    created() {
        this.tabMap = {};
        for (let i = 0; i < this.tabs.length; i++) {
            let tab = this.tabs[i];
            this.tabMap[tab.name] = tab;
        }

        this.getData();
    },

}
</script>

<style lang="scss">
.newui .dashboard {
    .my-forms-widget {
        .error-text {
            padding: 10px;
        }

        .ui-inbox {
            box-shadow: 0 0 0 transparent;
            //background: #f2f2f2;

            .ui-grid {
                .tag {
                    margin: 0 5px !important;
                    padding: 2px 5px !important;
                }

                .col-buttons {
                    white-space: nowrap;
                    text-align: right;
                }
            }

            .ui-inbox-labels {
                min-height: 210px;

                ul li.selected {
                    background-color: #fff;
                }
            }

            .ui-inbox-boxes {
                min-height: 210px;
            }
        }

        .ui-inbox-actions {
            width: 110px;
        }

        .pagination {
            margin-top: 15px;
        }
    }
}
</style>
